'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { ArrowLeft } from 'lucide-react';
import { FlowBuilder } from '@/components/flows/FlowBuilder';
import type { Node, Edge } from '@xyflow/react';
import { useToast } from '@/components/shared';

export default function FlowEditorPage() {
  const router = useRouter();
  const toast = useToast();
  const [flowName, setFlowName] = useState('New Flow');

  const handleSave = async (nodes: Node[], edges: Edge[]) => {
    try {
      // Transform to backend format
      const flowData = {
        name: flowName,
        nodes: nodes.map(node => ({
          id: node.id,
          type: node.type,
          data: node.data,
          position: node.position,
        })),
        edges: edges.map(edge => ({
          id: edge.id,
          source: edge.source,
          target: edge.target,
          sourceHandle: edge.sourceHandle,
          targetHandle: edge.targetHandle,
        })),
      };

      console.log('Saving flow:', flowData);
      
      // TODO: Save to backend
      // await adminBackendClient.saveFlow(flowData);
      
      toast.success('Flow saved successfully!');
    } catch (error) {
      console.error('Failed to save flow:', error);
      toast.error('Failed to save flow');
    }
  };

  const handleTest = () => {
    toast.info('Opening flow simulator...');
    // TODO: Open simulator modal
  };

  return (
    <div className="h-screen flex flex-col">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 px-6 py-4 flex items-center justify-between">
        <div className="flex items-center gap-4">
          <button
            onClick={() => router.back()}
            className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
          >
            <ArrowLeft size={20} />
          </button>
          <div>
            <input
              type="text"
              value={flowName}
              onChange={(e) => setFlowName(e.target.value)}
              className="text-2xl font-bold text-gray-900 bg-transparent border-none focus:outline-none focus:ring-2 focus:ring-indigo-500 rounded px-2"
            />
            <div className="text-sm text-gray-500">Visual Flow Editor</div>
          </div>
        </div>
      </div>

      {/* Flow Builder */}
      <div className="flex-1">
        <FlowBuilder
          flowId="new-flow"
          initialNodes={[]}
          initialEdges={[]}
          onSave={handleSave}
          onTest={handleTest}
        />
      </div>
    </div>
  );
}
